
import { Client } from "@/composables/client";
import router from "@/router";
import { toastSuccess } from "@/utils/Alerts";
import {
  IonButtons,
  IonButton,
  IonIcon,
  IonModal,
  IonHeader,
  IonContent,
  IonToolbar,
  IonTitle,
  IonItem,
  IonLabel,
  IonFooter,
  IonList,
  IonPage,
} from "@ionic/vue";

import { close } from "ionicons/icons";

import { PropType, handleError } from "vue";

import { defineComponent } from "vue";

export default defineComponent({
  components: {
    IonPage,
    IonButtons,
    IonButton,
    IonIcon,
    IonModal,
    IonHeader,
    IonContent,
    IonToolbar,
    IonTitle,
    IonFooter,
    IonItem,
    IonLabel,
    IonList,
  },
  data(): any {
    return {
      open: true as boolean,
      close: close,
    };
  },
  props: {
    order: {
      required: true,
      type: Object as PropType<any>,
    },
    show: {
      required: true,
      default: false,
      type: Boolean as PropType<any>,
    },
  },
  methods: {
    extractedTests(tests: Array<any>): string {
      return tests.map((test) => test.name).join(", ");
    },
    handleDialog(): void {
      this.open = false;
    },
    async changeSpecimenStatus(status: string): Promise<void> {
      const client = Client();

      const res = await client.putJson(`order_statuses/${status}`, {
        order_id: this.order.id,
      });

      if (res?.ok) {
        toastSuccess(`Specimen ${status} successfully!`);

        router.push("/home");
      }
    },
  },
});
